.rocket-container {
    width: 53px;
    padding: 25px 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: end;
        justify-content: flex-end;
}

.rocket-container__rocket {
    width: 24px;
    height: 26px;
    margin: 0 auto;
    -webkit-transition: var(--transition);
    -o-transition: var(--transition);
    transition: var(--transition);
}

.rocket {
    -webkit-transition: 0.3s ease-in-out;
    -o-transition: 0.3s ease-in-out;
    transition: 0.3s ease-in-out;
}

.track-container {
    height: 450px;
}

.track-container__track {
    display: block;
    margin: 0 auto;
    width: 1px;
    height: calc(445px / 4);
    background-color: var(--light_gray);
}

.track-container__track::after {
    content: '';
    display: block;
    width: 12px;
    height: 12px;
    background-color: var(--light_gray);
    -webkit-transform: translateX(-10.5px);
        -ms-transform: translateX(-10.5px);
            transform: translateX(-10.5px);
    border: 5px solid #fff;
    border-radius: 100%;
}

.track-container__track--completed {
    background-color: var(--accent_blue);
}

.track-container__track--completed::after {
    background-color: var(--accent_blue);
}


